@import url(https://fonts.googleapis.com/css?family=Amatic+SC|Indie+Flower|Montserrat:400,800|Open+Sans|Oswald);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  /*font style*/
  --font-title: "Oswald", sans-serif;
  --font-content1: "Indie Flower", cursive;
  --font-content2: "Amatic SC", cursive;
  /* color */
  --very-light-indigo : #dce3eb;
  --light-indigo: #577399;
  --dark-indigo : #364652;
  --light-red: #f55d3e;
  --dark-red : #b50c01;
  --light-blue : #bdd5ea;
  --vivid-blue : #76bed0;
  --nearly-white : #f7f7ff;
  --yellow : #f7cb15;
  --light-yellow : #fdf5d3;
}

.appWrapper {
  background-color: var(--dark-indigo);
  height: 100%;
}

.appMain {
  width: 80%;
  margin: 0 auto;
}

.wrapper {
  background-color: var(--dark-indigo);
  color: var(--nearly-white);
  margin-top: 1rem;
  padding-top: 3rem;
  padding-bottom: 3rem;
  text-align: center;
  width: 100%;
}

.postsWrapper {
  margin-top: 2rem;
}

.card {
  background-color: 'fff';
  color: var(--dark-indigo);
  border: 2px solid var(--light-indigo);
  margin: 1rem;
}

.cardBody:hover {
  background-color: var(--light-yellow);
  box-shadow: inset 0px 0px 10px var(--yellow);
}

.cardText {
  color: var(--dark-indigo);
  font-family: var(--font-content1);
  font-size: 1.5rem;
}

.cardLink {
  text-decoration: none;
}

.highlightText {
  color: var(--yellow);
  margin: auto 0.2rem;
}

.introFont {
  font-family: var(--font-title);
}

.introTextLeft {
  margin-right: 10rem;
  font-size: 1.7rem;
}

.introTextRight {
  margin-left: 10rem;
  font-size: 1.7rem;
}

.addPost {
  font-size: 4rem;
  color: var(--light-red);
  float: right;
  margin-right: 5rem;
}

.addPost:hover {
  color: var(--dark-red);
}

.editable_content_row__14bq3{
  margin : 1rem;
  font-family : var(--font-content1);
  font-size : 1.2rem;
  color : var(--dark-indigo);
}

.editable_content_title__k5zd2{
  font-weight : 800;
  font-family : var(--font-title);
  font-size : 1.3rem;
  color : var(--light-indigo);
}

.editable_edit_button__3aOGZ{
  background : var(--vivid-blue);
  border-radius : 10px;
  padding : 0.5rem 1rem;
  color : var(--nearly-white);
  float: right;
}

.editable_edit_button__3aOGZ:hover{
  background: var(--light-indigo);
  float: right;
}

.editable_content_body__1FCwc{
  background-color : var(--light-yellow);
  padding : 1rem;
  margin-bottom : 3rem;
}

.viewPostPage_post_wrapper__3XMDK{
  background-color : var(--dark-indigo);
  padding: 2rem;
}

.viewPostPage_jumbotron_hightlight__37f9M{
  border-top : 2rem solid var(--yellow);
}

.viewPostPage_delete_button__1z7RP{
  background-color : var(--light-red);
  color: var(--nearly-white);
  border-radius : 10px;
  padding : 0.5rem 1rem;
  margin-left : 0.5rem;
  float: right;
}

.viewPostPage_delete_button__1z7RP:hover{
  background-color : var(--dark-red);
}

.navBar {
  padding: 0 3rem;
}

#navBrand {
  font-weight: 900;
  font-size: 2rem;
  color: var(--light-indigo);
  font-family: var(--font-title);
}

.navLink {
  color: var(--light-indigo);
  font-weight: 700;
  font-size: 1.6rem;
  font-family: var(--font-title);
}

.navLink:hover {
  border-bottom: 3px solid var(--yellow);
  box-shadow: 2px 2px 2px #ccc;
  padding: 0.7rem 0.5rem;
  background-color: var(--light-blue);
  color: var(--dark-indigo);
}

.navLink:active {
  border-bottom: 3px solid var(--light-red);
}

.card_wrapper {
  padding: 5rem 1rem;
  background-color: var(--dark-indigo);
}

.infoCard {
  padding: 1rem;
}

.textTitle {
  font-weight: 700;
  color: var(--dark-indigo);
  font-size: 1.1rem;
}

.timeToRight {
  float: right;
}

